import {createNode} from './nodeService';
const body = document.body;
const html = document.documentElement;

const getHeight = () => {
  const height = Math.max( body.scrollHeight, body.offsetHeight,
      html.clientHeight, html.scrollHeight, html.offsetHeight );
  return height;
};

const getWidth = () => {
  const width = Math.max( body.scrollWidth, body.offsetWidth,
      html.clientWidth, html.scrollWidth, html.offsetWidth );
  return width;
};

export const getRows = () => {
  const height = getHeight();
  return Math.floor(height/ 30);
};

export const getCols = () => {
  const width = getWidth();
  return Math.floor(width / 30);
};

const ROWS = getRows();
const COLS = getCols();
const MIDDLE_ROW = Math.floor(ROWS / 2);
const MIDDLE_COL = Math.floor(COLS / 2);
const START_ROW = MIDDLE_ROW;
const START_COL = MIDDLE_COL - 5;
const END_ROW = MIDDLE_ROW;
const END_COL = MIDDLE_COL + 5;

export const getInitialStartNodeDimensions = () => {
  return {row: START_ROW, col: START_COL};
};

export const getInitialEndNodeDimensions = () => {
  return {row: END_ROW, col: END_COL};
};


export const generateGrid = () => {
  const grid = [];
  for (let i = 0; i < ROWS; i++) {
    grid.push([]);
    for (let j = 0; j < COLS; j++) {
      grid[i].push(createNode(i, j, START_ROW, START_COL, END_ROW, END_COL));
    }
  }
  return grid;
};
