export default function dijkstrasAlgorithm(startRow, startCol,
    endRow, endCol, graph) {
  const visitedNodes = [];
  const nodes = initializeNodes(graph);

  const startNode = nodes[startRow][startCol];
  const endNode = nodes[endRow][endCol];

  startNode.distance = 0;

  const unexploredNodes = new MinHeap([startNode]);

  while (!unexploredNodes.isEmpty()) {
    const currentNode = unexploredNodes.remove();
    currentNode.isVisited = true;
    currentNode.inQue = false;

    if (currentNode === endNode) {
      return {path: reconstructPath(endNode).reverse(),
        visitedNodes: visitedNodes};
    }

    const neighbours = getNeighbouringNodes(currentNode, nodes);

    for (let i = 0; i < neighbours.length; i++) {
      const neighbour = neighbours[i];
      if (neighbour.isWall || neighbour.isVisited) continue;
      updateNeighbourDistance(currentNode, neighbour, unexploredNodes,
          visitedNodes);
    }
  }
}

function reconstructPath(node) {
  const path = [];
  let tempNode = node;

  while (tempNode !== null) {
    path.push([tempNode.row, tempNode.col]);
    tempNode = tempNode.previousNode;
  }
  return path;
}

function updateNeighbourDistance(node, neighbour, unexploredNodes,
    visitedNodes) {
  const currentNeighbourDistance = node.distance + neighbour.weight;
  if (currentNeighbourDistance < neighbour.distance) {
    neighbour.distance = currentNeighbourDistance;
    neighbour.previousNode = node;
    if (unexploredNodes.containsNode(neighbour)) {
      unexploredNodes.update(neighbour);
    } else {
      unexploredNodes.insert(neighbour);
    }
    visitedNodes.push(neighbour);
  }
}

function getNeighbouringNodes(node, graph) {
  const row = node.row;
  const col = node.col;

  const neighbours = [];

  if (row > 0) {
    neighbours.push(graph[row - 1][col]);
  }

  if (row < graph.length - 1) {
    neighbours.push(graph[row + 1][col]);
  }

  if (col > 0) {
    neighbours.push(graph[row][col - 1]);
  }

  if (col < graph[0].length - 1) {
    neighbours.push(graph[row][col + 1]);
  }
  return neighbours;
}

function initializeNodes(graph) {
  const nodes = [];
  for (let i = 0; i < graph.length; i++) {
    nodes.push([]);
    for (let j = 0; j < graph[0].length; j++) {
      const currentNode = graph[i][j];
      const newNode = {
        ...currentNode,
        distance: Infinity,
        previousNode: null,
        weight: currentNode.isWeighted ? 5 : 1,
        inQue: true,
        id: currentNode.row.toString() + '-' + currentNode.col.toString(),
      };
      nodes[i].push(newNode);
    }
  }
  return nodes;
}


class MinHeap {
  constructor(array) {
    this.nodePositionsInHeap = array.reduce((obj, node, i) => {
      obj[node.id] = i;
      return obj;
    }, {});
    this.heap = this.buildHeap(array);
    this.visitedNodes = [];
  }

  isEmpty() {
    return this.heap.length == 0;
  }

  buildHeap(array) {
    const firstParentIdx = Math.floor((array.length - 2) / 2);
    for (let currentIdx = firstParentIdx; currentIdx >= 0; currentIdx--) {
      this.siftDown(currentIdx, array.length - 1, array);
    }
    return array;
  }

  siftDown(currentIdx, endIdx, heap) {
    let childOneIdx = currentIdx * 2 + 1;
    while (childOneIdx <= endIdx) {
      const childTwoIdx = currentIdx * 2 + 2 <= endIdx ?
        currentIdx * 2 + 2 : -1;
      let idxToSwap;
      if (childTwoIdx !== -1 && heap[childTwoIdx].distance <
          heap[childOneIdx].distance) {
        idxToSwap = childTwoIdx;
      } else {
        idxToSwap = childOneIdx;
      }
      if (heap[idxToSwap].distance <
          heap[currentIdx].distance) {
        this.swap(currentIdx, idxToSwap, heap);
        currentIdx = idxToSwap;
        childOneIdx = currentIdx * 2 + 1;
      } else {
        return;
      }
    }
  }

  siftUp(currentIdx, heap) {
    let parentIdx = Math.floor((currentIdx - 1) / 2);
    while (currentIdx > 0 && heap[currentIdx].distance <
          heap[parentIdx].distance) {
      this.swap(currentIdx, parentIdx, heap);
      currentIdx = parentIdx;
      parentIdx = Math.floor((currentIdx - 1) / 2);
    }
  }

  remove() {
    if (this.isEmpty()) return;

    this.swap(0, this.heap.length - 1, this.heap);
    const node = this.heap.pop();
    delete this.nodePositionsInHeap[node.id];
    this.siftDown(0, this.heap.length - 1, this.heap);
    return node;
  }

  insert(node) {
    this.visitedNodes.push(node);
    this.heap.push(node);
    this.nodePositionsInHeap[node.id] = this.heap.length - 1;
    this.siftUp(this.heap.length - 1, this.heap);
  }

  swap(i, j, heap) {
    this.nodePositionsInHeap[this.heap[i].id] = j;
    this.nodePositionsInHeap[this.heap[j].id] = i;
    const temp = heap[j];
    heap[j] = heap[i];
    heap[i] = temp;
  }

  containsNode(node) {
    return node.id in this.nodePositionsInHeap;
  }

  update(node) {
    this.siftUp(this.nodePositionsInHeap[node.id], this.heap);
  }
}
