import React from 'react';

import '../grid.css';

const Node = ({row, col, isStart, isEnd, isWall, isVisited, isSolved,
  isWeighted, onMouseDown, onMouseEnter, onMouseUp}) => {
  const getExtraClass = () => {
    if (isStart) return 'start-node';
    if (isEnd) return 'end-node';
    if (isWeighted) return 'weighted-node';
    if (isWall) return 'wall-node';
    if (isSolved) return 'solved-node';
    if (isVisited) return 'visited-node';
    return '';
  };

  return (
    <div id={`${row}-${col}`} className={`node ${getExtraClass()}`}
      onMouseDown={() => onMouseDown(row, col)}
      onMouseEnter={() => onMouseEnter(row, col)}
      onMouseUp={() => onMouseUp()}
    >
    </div>
  );
};

export default Node;
