import React from 'react';
import Draggable from 'react-draggable';

import AuthenticationButton from '../AuthenticationButton';
import AlgorithmSelector from './AlgorithmSelector';
import GridInterfaceButtons from './GridInterfaceButtons';
import GridInterfaceWeightedNodes from './GridInterfaceWeightedNodes';

// import Paper from '@mui/material/Paper';

const gridInterfaceStyle = {
  height: 200,
  width: 200,
  border: '1px solid black',
  position: 'absolute',
  backgroundColor: 'black',
  opacity: 0.7,
  top: 0,
};

const GridInterface = ({solveMazeHandler, resetMazeHandler,
  weightedNode, setWeightedNode, removeWeightedNodes, algorithm,
  setAlgorithm, grid}) => {
  const handleAlgorithmChange = (event) => {
    if (event.target.value !== 'Dijkstras') {
      removeWeightedNodes(grid);
    }
    setAlgorithm(event.target.value);
  };

  const weightedNodeHandler = () => {
    setWeightedNode(!weightedNode);
  };

  return (
    <div>
      <Draggable>
        <div style={gridInterfaceStyle}>
          <div style={{color: 'white'}}>
        Hiteshs PathFindingVisualizer
          </div>
          <AlgorithmSelector
            algorithm={algorithm}
            handleAlgorithmChange={handleAlgorithmChange}
          />
          <AuthenticationButton />
          <GridInterfaceButtons
            algorithm={algorithm}
            solveMazeHandler={solveMazeHandler}
            resetMazeHandler={resetMazeHandler}
          />
          <GridInterfaceWeightedNodes
            weightedNodeHandler={weightedNodeHandler} algorithm={algorithm}
          />
        </div>
      </Draggable>
    </div>
  );
};

export default GridInterface;
