import React, {useEffect, useState} from 'react';

import Node from './Node';
import '../grid.css';
import {generateGrid,
  getInitialStartNodeDimensions,
  getInitialEndNodeDimensions,
} from '../services/gridService';
import {gridContainerStyle} from '../services/styleService';
import GridInterface from './GridInterface/GridInterface';
import {solveMazeHandler,
  resetMazeHandler, removeWeightedNodes} from '../services/nodeService';
import {handleMouseDown, handleMouseEnter,
  handleMouseUp} from '../services/eventHandlers/PathFindingVisualizerHandler';

const PathFindingVisualizer = () => {
  const [grid, setGrid] = useState(generateGrid());
  const [mouseIsPressed, setMousePressed] = useState(false);
  const [startNodeSelected, setStartNodeSelected] = useState(false);
  const [startNode, setStartNode] = useState(getInitialStartNodeDimensions);
  const [endNodeSelected, setEndNodeSelected] = useState(false);
  const [endNode, setEndNode] = useState(getInitialEndNodeDimensions);
  const [weightedNode, setWeightedNode] = useState(false);
  const [algorithm, setAlgorithm] = useState('Dijkstras');

  useEffect(() => {
    if (algorithm !== 'Dijkstras') {
      setWeightedNode(false);
    }
  }, [algorithm]);

  return (
    <div style={gridContainerStyle}>
      { grid.map((row, rowIdx) => row.map((node, colIdx) => {
        const {row, col, isStart, isEnd,
          isWall, isVisited, isSolved, isWeighted} = node;
        return (
          <div key={`${rowIdx}-${colIdx}`}>
            <Node
              row={row}
              col={col}
              isStart={isStart}
              isEnd={isEnd}
              isWall={isWall}
              isVisited={isVisited}
              isSolved={isSolved}
              mouseIsPressed={mouseIsPressed}
              isWeighted={isWeighted}
              onMouseDown={(row, col) => handleMouseDown(row, col, grid,
                  setGrid, setMousePressed, setStartNodeSelected,
                  setEndNodeSelected, weightedNode)}
              onMouseEnter={(row, col) => handleMouseEnter(row, col, grid,
                  setGrid, mouseIsPressed, startNodeSelected, endNodeSelected,
                  setStartNode, startNode, setEndNode, endNode, weightedNode)}
              onMouseUp={() => handleMouseUp(setStartNodeSelected,
                  setEndNodeSelected, setMousePressed)}
            />
          </div>
        );
      }))}
      <div>
        <GridInterface
          solveMazeHandler={(algorithm) => solveMazeHandler(grid, startNode,
              endNode, algorithm)}
          resetMazeHandler={() => resetMazeHandler(setGrid,
              setStartNode, setEndNode)}
          weightedNode={weightedNode}
          setWeightedNode={setWeightedNode}
          removeWeightedNodes={removeWeightedNodes}
          algorithm={algorithm}
          setAlgorithm={setAlgorithm}
          grid={grid}
        />
      </div>
    </div>
  );
};

export default PathFindingVisualizer;
