import React from 'react';
import {Select, MenuItem,
  InputLabel} from '@material-ui/core';

const AlgorithmSelector = ({algorithm, handleAlgorithmChange}) => {
  return (
    <div>
      <InputLabel id="demo-simple-select-label"
        style={{color: 'white'}}>Algorithm</InputLabel>
      <Select
        renderValue={() => algorithm}
        labelId="demo-simple-select-label"
        id="demo-simple-select"
        value={algorithm}
        onChange={(e) => handleAlgorithmChange(e)}
        style={{color: 'white'}}
      >
        <MenuItem value={'Dijkstras'}>dijkstras</MenuItem>
        <MenuItem value={'aStar'}>aStar</MenuItem>
      </Select>ยง
    </div>
  );
};

export default AlgorithmSelector;
