import aStarAlgorithm from './Algorithms/astar';
import dijkstrasAlgorithm from './Algorithms/dijkstras';

export const aStar = (startRow, startCol, endRow, endCol, graph) => {
  return aStarAlgorithm(startRow, startCol, endRow, endCol, graph);
};

export const dijkstras = (startRow, startCol, endNodeRow, endNodeCol, grid) => {
  return dijkstrasAlgorithm(startRow, startCol, endNodeRow, endNodeCol, grid);
};
