import React from 'react';
import Button from '@material-ui/core/Button';
import {aStar, dijkstras} from '../../services/algorithmService';

const getAlgorithmFunction = (algorithm) => {
  if (algorithm === 'Dijkstras') return dijkstras;
  if (algorithm === 'aStar') return aStar;
};

const GridInterfaceButtons = ({algorithm,
  solveMazeHandler, resetMazeHandler}) => {
  return (
    <div>
      <Button
        variant='contained'
        color='primary'
        onClick={() => solveMazeHandler(getAlgorithmFunction(algorithm))}
        className="solve-maze-button"
      >
            Solve
      </Button>
      <Button
        variant='contained'
        color='secondary'
        onClick={() => resetMazeHandler()}
        className="solve-maze-button"
      >
              Reset
      </Button>
    </div>
  );
};

export default GridInterfaceButtons;
