import {generateGrid} from './gridService';
import {getInitialStartNodeDimensions,
  getInitialEndNodeDimensions} from './gridService';

export const createNode = (row, col, START_ROW,
    START_COL, END_ROW, END_COL) => {
  return {
    row: row,
    col: col,
    isStart: row == START_ROW && col == START_COL,
    isEnd: row == END_ROW && col == END_COL,
    isWall: false,
    isVisited: false,
    isSolved: false,
    isWeighted: false,
  };
};

export const wallHandler = (row, col, grid, setGrid) => {
  setTimeout(() => {
    const newGrid = grid.slice();
    const node = grid[row][col];
    const newNode = {
      ...node,
      isWall: !node.isWall,
    };
    newGrid[node.row][node.col] = newNode;
    setGrid(newGrid);
  }, 50);
};

export const weightedNodeHandler = (row, col, grid, setGrid) => {
  setTimeout(() => {
    const newGrid = grid.slice();
    const node = grid[row][col];
    const newNode = {
      ...node,
      isWeighted: !node.isWeighted,
    };
    newGrid[node.row][node.col] = newNode;
    setGrid(newGrid);
  }, 50);
};

const animateVisitedNodes =({path, visitedNodes}, grid) => {
  for (let i = 0; i <= visitedNodes.length; i++) {
    if (i == visitedNodes.length) {
      setTimeout(() => {
        animateShortestPath(path, grid);
      }, 15 * i);
      return;
    }
    setTimeout(() => {
      const visitedNode = visitedNodes[i];
      const currentNode = grid[visitedNode.row][visitedNode.col];
      if (!currentNode.isStart && !currentNode.isEnd &&
        !currentNode.isWeighted) {
        if (!visitedNode.inQue) {
          document
              .getElementById(`${visitedNode.row}-${visitedNode.col}`)
              .className = 'node visited-node';
        } else {
          document
              .getElementById(`${visitedNode.row}-${visitedNode.col}`)
              .className = 'node visited-node-in-que';
        }
      }
    }, 15 * i);
  }
};

export const removeWeightedNodes = (grid) => {
  const visitedNodesInQue = document.querySelectorAll('.weighted-node');
  for (let i = 0; i < visitedNodesInQue.length; i++) {
    visitedNodesInQue[i].classList.remove('weighted-node');
  }
  grid.map((row) => row.map((col) => {
    col.isWeighted = false;
  }));
};


const animateShortestPath = (path, grid) => {
  for (let i = 0; i < path.length; i++) {
    const currentNode = grid[path[i][0]][path[i][1]];
    setTimeout(() => {
      if (!currentNode.isStart && !currentNode.isEnd) {
        document
            .getElementById(`${path[i][0]}-${path[i][1]}`)
            .className = 'node solved-node';
      }
    }, 20 * i);
  }
};

export const moveNode = (previousNode, currentNode, value, grid, setGrid) => {
  const newGrid = grid.slice();
  // Start or end node
  const oldNode = {
    ...previousNode,
    [value]: false,
  };
    // New start or end node
  const newNode = {
    ...currentNode,
    [value]: true,
  };
    // newNode -> startNode
  newGrid[currentNode.row][currentNode.col] = newNode;
  newGrid[previousNode.row][previousNode.col] = oldNode;
  setGrid(newGrid);
};

export const solveMazeHandler = (grid, startNode, endNode, algorithm) => {
  resetPreviousMazeHandler();
  const visitedNodesInOrder = algorithm(startNode.row, startNode.col,
      endNode.row, endNode.col, grid);
  animateVisitedNodes(visitedNodesInOrder, grid);
};

export const resetPreviousMazeHandler = () => {
  // Removes visited nodes
  const visitedNodes = document.querySelectorAll('.visited-node');
  for (let i = 0; i < visitedNodes.length; i++) {
    visitedNodes[i].classList.remove('visited-node');
  }
  removeVisitedNodesInQue();
  removeSolvedNodes();
};

export const resetMazeHandler = (setGrid, setStartNode, setEndNode) => {
  // Removes visited nodes
  const visitedNodes = document.querySelectorAll('.visited-node');
  for (let i = 0; i < visitedNodes.length; i++) {
    visitedNodes[i].classList.remove('visited-node');
  }
  removeVisitedNodesInQue();
  removeSolvedNodes();
  setGrid(generateGrid());
  resetBeginningNodes(setStartNode, setEndNode);
};

const removeVisitedNodesInQue = () => {
  const visitedNodesInQue = document.querySelectorAll('.visited-node-in-que');
  for (let i = 0; i < visitedNodesInQue.length; i++) {
    visitedNodesInQue[i].classList.remove('visited-node-in-que');
  }
};

const resetBeginningNodes = (setStartNode, setEndNode) => {
  setStartNode(getInitialStartNodeDimensions);
  setEndNode(getInitialEndNodeDimensions);
};

// Removes the path of solved nodes
const removeSolvedNodes = () => {
  const solvedNodes = document.querySelectorAll('.solved-node');
  for (let i = 0; i < solvedNodes.length; i++) {
    solvedNodes[i].classList.remove('solved-node');
  }
};
