import {moveNode, wallHandler, weightedNodeHandler} from '../nodeService';

export const handleMouseDown = (row, col, grid, setGrid,
    setMousePressed, setStartNodeSelected, setEndNodeSelected,
    weightedNode) => {
  const currentNode = grid[row][col];
  setMousePressed(true);
  // Check if start node is selected
  if (currentNode.isStart) {
    // setStartNode({row: row, col: col});
    setStartNodeSelected(true);
  } else if (currentNode.isEnd) {
    setEndNodeSelected(true);
  } else if (weightedNode) {
    weightedNodeHandler(row, col, grid, setGrid);
  } else {
    wallHandler(row, col, grid, setGrid);
  }
};

export const handleMouseEnter = async (row, col, grid, setGrid, mouseIsPressed,
    startNodeSelected, endNodeSelected,
    setStartNode, startNode, setEndNode, endNode,
    weightedNode) => {
  if (!mouseIsPressed) return;
  // Check if start node is selected
  const currentNode = grid[row][col];
  if (startNodeSelected) {
    // replace the start node
    const previousStartNode = grid[startNode.row][startNode.col];
    moveNode(previousStartNode, currentNode, 'isStart', grid, setGrid);
    setStartNode({row: row, col: col});
  } else if (endNodeSelected) {
    const previousNode = grid[endNode.row][endNode.col];
    moveNode(previousNode, currentNode, 'isEnd', grid, setGrid);
    setEndNode({row: row, col: col});
  } else if (weightedNode) {
    weightedNodeHandler(row, col, grid, setGrid);
  } else {
    wallHandler(row, col, grid, setGrid);
  }
};

export const handleMouseUp = (setStartNodeSelected, setEndNodeSelected,
    setMousePressed) => {
  setStartNodeSelected(false);
  setEndNodeSelected(false);
  setMousePressed(false);
};
