import React from 'react';
import {Checkbox, FormControlLabel, FormGroup} from '@material-ui/core';

const GridInterfaceWeightedNodes = ({weightedNodeHandler, algorithm}) => {
  if (algorithm !== 'Dijkstras') {
    return null;
  }
  return (
    <div>
      <FormGroup>
        <FormControlLabel style={{color: 'white', textColor: 'white'}}
          control={<Checkbox
            style={{color: 'white', textColor: 'white'}}
          />}
          label="Weighted node"
          onChange={() => weightedNodeHandler()} />
      </FormGroup>
    </div>
  );
};

export default GridInterfaceWeightedNodes;
